@import url(https://fonts.googleapis.com/css?family=Lakki+Reddy&display=swap);
#navBar{
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--dark-color);
    color: var(--light-color);

    z-index: 2;
}

#navBar .logo{
    text-transform: uppercase;
}

#navBar .nav-list{
    display: flex;
}

#navBar .nav-list li{
    padding: 0 1rem;
}
#heroHead{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: url("https://i.kym-cdn.com/entries/icons/original/000/016/850/CartoonNetwork.jpg") center center/cover no-repeat;
    height: 50vh;
    position: relative;
    z-index: 1;
}

#heroHead:before{
    content:'';
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.6);
    position: absolute;
    z-index: -1;
}

#heroHead .hero-container{
    color:rgb(122, 204, 231);
    
}

#heroHead .hero-container h1{
    font-size: 3rem;
}

#heroHead .hero-container p{
    font-size: 2.4rem;
}

#shows{
    width:100%;
}
img{
    width:100%;
    height: 100%;
    display: block;
}

.container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 100%;
    grid-gap: 0;
    margin: 0 auto;
    justify-items: center;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
 
}

.card{
    padding: 1rem 0;
    width: 500px;
    height: 300px;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
}

.card img{
    width:368px;
    height: 270px;
    border-radius: 5%;
    -webkit-transform: translateX(-10%) translateY(-4%);
            transform: translateX(-10%) translateY(-4%);
}

.card::before{
    content:'';
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    background: url("https://images-na.ssl-images-amazon.com/images/I/81sAk4juJzL._SL1500_.jpg") no-repeat center center/cover;
    z-index:-1;

}

#shows .score{
    background: var(--dark-color);
    color:var(--light-color);

}

#shows .score .score-list{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:0;
    
}

#shows .score .score-list li{
    margin: 0 1rem;
}

#foot{
    background: var(--dark-color);
    color:var(--light-color);
}

#foot .foot-list{
    display: flex;
    justify-content: center;
}


#foot .foot-list li{
    margin: 0 1rem;
}

*{
  --max-width: 1100px;
  --dark-color: #111;
  --light-color: ivory;
  --main-font: 'Lakki Reddy', cursive; 
}

body{
  margin:0;
  padding: 0;
  font-family:var(--main-font); 
}

h1{
  font-size:2rem;
}

ul{
  margin:0;
}
li{
  font-size: 1.5rem;
  list-style-type: none;
}

a{
  text-decoration: none;
  color: var(--light-color);
  font-size: 1.5rem;
}
/* Utility */
.my-1-all{
  margin: 1rem;
}
.my-2-all{
  margin: 2rem;
}

.my-1-side{
  margin: 0 1rem;
}
.my-2-side{
  margin: 0 2rem;
}

.py-1{
  padding: 1rem;
}
.py-2{
  padding: 2rem;
}
.py-3{
  padding: 3rem;
}

